import { MicroAppCustomProps, MicroAppContext } from '@wppopen/core'
import { AsyncMethodReturns, connectToParent, Methods } from 'penpal'
import { useContext, useState, useEffect, createContext, PropsWithChildren } from 'react'

import { LoadingPage } from 'app/loadingPage/LoadingPage'
import { FramedMicroAppMethods, FramedMicroAppParentMethods } from 'app/types'

export interface OsContextValue {
  osContext: MicroAppContext
  osApi: AsyncMethodReturns<MicroAppCustomProps['osApi']>
}

const OsContext = createContext<OsContextValue>(null!)

export const OsProvider = ({ children }: PropsWithChildren<{}>) => {
  const [osApiValue, setOsApiValue] = useState<AsyncMethodReturns<MicroAppCustomProps['osApi']> | null>(null)
  const [osContextValue, setOsContextValue] = useState<MicroAppContext | null>(null)

  useEffect(() => {
    const methods: Methods = {
      receiveOsContext: osContext => {
        console.log('%creceiveOsContext', 'padding:5px 10px; background: #ffaa8c;')
        console.log(osContext)

        setOsContextValue(osContext)
      },
    } satisfies FramedMicroAppMethods

    const connection = connectToParent<FramedMicroAppParentMethods>({
      parentOrigin: '*',
      methods,
      debug: true,
    })

    connection.promise.then(({ osApi }) => {
      console.log('%cChild received api methods', 'padding:5px 10px; background: #eda9d2;')
      setOsApiValue(osApi)
    })

    connection.promise.catch(error => {
      console.error('Penpal child context error:', error)
    })

    return () => {
      console.log('%cDestroy child connection', 'padding:5px 10px; background: #96d8be;')
      connection.destroy()
    }
  }, [])

  if (!osContextValue || !osApiValue) {
    return <LoadingPage />
  }

  return (
    <OsContext.Provider
      value={{
        osContext: osContextValue,
        osApi: osApiValue,
      }}
    >
      {children}
    </OsContext.Provider>
  )
}

export const useOs = () => useContext(OsContext)
